export const tradeService = {
    query,
    remove,
    add

}

async function query() {
    var data = JSON.parse(localStorage.getItem(KEY)) || []
    if (data && data.length) return Promise.resolve(data)

    // Since i deleted my API-KEY from this file before deploying to Github, the data return from global var, the code here is my API call to get it originally the API request:
    // const comps = ['Arbe', 'Facebook', 'Google', 's&p 500']
    // comps.forEach(comp => {
    //     try {
    //         _loadTrade(comp)
    //             .then(compdata => {
    //                 data.push(compdata)
    //             })
    //     } catch (err) {
    //         console.log(err);
    //     }
    // })
    // localStorage.setItem(KEY, JSON.stringify(data))
    localStorage.setItem(KEY, JSON.stringify(gComps))
    data = Promise.resolve(gComps)
    return data
}

async function remove(compId) {
    try {
        var comps = await query()
        var uptadedComps = comps.filter(comp => comp.quotes[0].symbol !== compId)
        localStorage.setItem(KEY, JSON.stringify(uptadedComps))
        return uptadedComps
    } catch (err) {
        console.log(err);
    }
}

function add(comp) {
    var comps = JSON.parse(localStorage.getItem(KEY)) || []
    comps.push(comp)
    localStorage.setItem(KEY, JSON.stringify(comps))
    return Promise.resolve(comps)
}


function _loadTrade(comp) {
    const options = {
        method: 'GET',
        url: 'https://yh-finance.p.rapidapi.com/auto-complete',
        params: { q: comp, region: 'US' },
        headers: {
            'x-rapidapi-host': 'yh-finance.p.rapidapi.com',
            'x-rapidapi-key': API_KEY
        }
    };

    return axios.request(options).then((response) => {
        return response.data
    }).catch(function (error) {
        console.error(error);
    });
}


