<template>
  <tr class="trade-preview">
    <td>
      <button class="delete-btn" @click="onRemoveComp"></button>
    </td>
    <td>{{ comp.quotes[0].symbol }}</td>
    <td>{{ comp.quotes[0].shortname }}</td>
    <td>{{ comp.quotes[0].quoteType }}</td>
    <td>{{ comp.quotes[0].exchange }}</td>
    <td>{{ comp.quotes[0].score }}</td>
    <td>
      <p>
        <span>{{ comp.news[0].publisher }}:</span>
        {{ comp.news[0].title }}
      </p>
      <a :href="comp.news[0].link">read more</a>
    </td>
  </tr>
</template>

<script>
export default {
  props: ["comp"],
  methods: {
    onRemoveComp() {
      this.$emit("removeComp", this.comp.quotes[0].symbol);
    },
  },
};
</script>

