<template>
  <section class="main-layout">
    <trade-table v-if="comps" :comps="comps" @removeComp="removeComp" />
  </section>
</template>

<script>
import tradeTable from "../cmps/trade-table/trade-table";
import { tradeService } from "./../services/trade-service.js";

export default {
  data() {
    return {
      comps: [],
    };
  },
  created() {
    tradeService.query().then((res) => {
      this.comps = res;
      console.log(res);
    });
  },
  methods: {
    async removeComp(compId) {
      try {
        var uptatedComps = await tradeService.remove(compId);
        this.comps = uptatedComps;
      } catch (err) {
        console.log(err);
      }
    },
  },
  components: { tradeTable },
};
</script>

