
<template>
  <header :class="['app-header', navClass]">
    <app-screen @click.native="toggleNav"></app-screen>
    <div class="header-container flex space-between align-center main-layout">
      <h1>Trades App</h1>
    </div>
  </header>
</template>

<script>
import appScreen from "./app-screen";
export default {
  data() {
    return {
      isNavOpen: false,
    };
  },
  methods: {
    toggleNav() {
      this.isNavOpen = !this.isNavOpen;
    },
  },
  computed: {
    navClass() {
      if (this.isNavOpen) return "open";
    },
  },
  components: {
    appScreen,
  },
};
</script>

