<template>
  <table class="trade-table">
    <thead>
      <trade-headers v-for="tHead in tHeads" :key="tHead.val" :tHead="tHead" />
    </thead>
    <tbody>
      <trade-row
        v-for="comp in comps"
        :key="comp.quotes[0].symbol"
        :comp="comp"
        @removeComp="removeComp"
      />
    </tbody>
  </table>
</template>

<script>
import { tradeService } from "../../services/trade-service";
import tradeHeaders from "./trade-headers";
import TradeRow from "./trade-row.vue";
export default {
  props: ["comps"],
  data() {
    return {
      tHeads: [
        { val: "delete", txt: "" },
        { val: "symbol", txt: "Symbol" },
        { val: "shortname", txt: "Name" },
        { val: "quoteType", txt: "Quote type" },
        { val: "exchange", txt: "Exchange" },
        { val: "score", txt: "Score" },
        { val: "news", txt: "Lastest news" },
      ],
    };
  },
  methods: {
    removeComp(compId) {
      this.$emit("removeComp", compId);
    },
  },
  components: { tradeHeaders, TradeRow },
};
</script>
